.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    cursor: pointer;
    max-height: 200px;
    margin-right: 10px;
}

.smallPoster{
    cursor: pointer;
    max-height: 150px;
    margin-right: 10px;

}

.movieTitle {
    font-size: 16px;
    margin-top: 5px;
  }

.poster:hover{
    transform: scale(1.1);
}

.smallPoster:hover{
    transform: scale(1.1);
}