 .banner{
    background-size: cover;
    /* background-position: center; */
    height: 600px;
    color: white;

}

.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.button{
    color: white;
    outline: none;
    border: none;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
    margin-top: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 4rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}

.fade_bottom{
    height: 17rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,.1),#111);
}

